import React from 'react'
import { Button } from 'antd'

export default function TableTool() {
  return (
    <div className="m-content-table-tool">
      <div className="m-content-table-tool-row">
        <Button className="m-btn" type="primary">
          添加商户
        </Button>
        <Button className="m-btn" type="primary">
          快速添加商户
        </Button>
      </div>
      <div className="m-content-table-tool-row">
        <Button className="m-btn" type="primary">
          批量设置运营人员
        </Button>
      </div>
    </div>
  )
}
