import React, { useEffect, useState } from 'react'
import { Table, Button } from 'antd'
import Api from '../.././../../../../api'
import { columns } from './config'
import TableTool from './TableTool'

export default function CompanyList(props) {
  const [list, setList] = useState([])
  const [total, setTotal] = useState()
  const [current, setCurrent] = useState(1)
  const [pageSize, setPageSize] = useState(10)

  const handlePage = (page, pageSize) => {
    Api.light.company({ page, size: pageSize }, 'get').then((res) => {
      if (res.code === 200) {
        setList(res.data.list)
        setTotal(res.data.total)
        setCurrent(res.data.current)
        setPageSize(res.data.pageSize)
      }
    })
  }

  const handleDelete = (record) => {
    console.log('删除, id:', record.id)
  }

  const handleEdit = (record) => {
    console.log('编辑, id:', record.id)
  }

  const action = {
    title: '操作',
    width: 220,
    render: (record) => {
      return (
        <div className="m-action">
            <Button className="m-action-btn"  size="small" danger onClick={() => handleDelete(record)}>
              删除
            </Button>
            <Button className="m-action-btn"  size="small">查看</Button>
            <Button className="m-action-btn"  size="small" onClick={() => handleEdit(record)}>
              编辑
            </Button>
            <Button className="m-action-btn"  size="small">预览</Button>
          <Button className="m-action-btn"  size="small">店铺管理</Button>
          <Button className="m-action-btn"  size="small">课程管理</Button>
          <Button className="m-action-btn"  size="small">老师管理</Button>
          <Button className="m-action-btn"  size="small">百度更新</Button>
          <Button className="m-action-btn"  size="small">百度查看</Button>
          <Button className="m-action-btn"  size="small">更新电话</Button>
        </div>
      )
    },
  }

  useEffect(() => {
    handlePage(current, pageSize)
  }, [props])
  return (
    <div className="m-table-wrap">
      <TableTool></TableTool>
      <Table
        className="m-my-table"
        columns={[...columns, action]}
        dataSource={list}
        rowKey="id"
        scroll={{ scrollToFirstRowOnChange: true, x: 800 }}
        pagination={{
          current,
          total,
          pageSize,
          onChange: (page, pageSize) => handlePage(page, pageSize),
          showSizeChanger: true,
          pageSizeOptions: [10, 20, 50],
          onShowSizeChange: (current, size) => handlePage(current, size),
          showTotal: (total) => `共有数据 ${total} 条`,
        }}
      ></Table>
    </div>
  )
}
